@font-face {
  font-family: "gilroy";
  src: url("../fonts/gilroy-bold.woff") format("woff"), url("../fonts/gilroy-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "gilroy";
  src: url("../fonts/gilroy-semibold.woff") format("woff"), url("../fonts/gilroy-semibold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "gilroy";
  src: url("../fonts/gilroy-medium.woff") format("woff"), url("../fonts/gilroy-medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "gilroy";
  src: url("../fonts/gilroy-regular.woff") format("woff"), url("../fonts/gilroy-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "gilroy";
  src: url("../fonts/gilroy-ultralight.woff") format("woff"), url("../fonts/gilroy-ultralight.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
/* Global */
:root {
  box-sizing: border-box;
  font-size: 1em;
  line-height: 1.375;
}
@media (max-width: 1600px) {
  :root {
    font-size: 0.875em;
  }
}
@media (max-width: 768px) {
  :root {
    font-size: 0.875em;
    line-height: 1.571;
  }
}
@media (max-width: 480px) {
  :root {
    font-size: 0.625em;
    line-height: 1.5;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.content-box-component {
  box-sizing: content-box;
}

html {
  height: 100%;
  font-family: "gilroy", Arial, sans-serif;
  font-weight: 400;
  letter-spacing: 0.03em;
  color: #333333;
}

body {
  height: 100%;
}

button,
input {
  outline: none;
  border: none;
  cursor: pointer;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

ul li,
ol li {
  list-style: none;
}

dd {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}
a.green-link {
  position: relative;
  color: #4ECEA9;
}
a.green-link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  width: 100%;
  height: 1px;
  background-color: #4ECEA9;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  position: relative;
  min-height: 100%;
  min-width: 320px;
  overflow: hidden;
}

.wrapper {
  opacity: 0;
  transition: opacity 1s;
}

.wrapper_loaded {
  opacity: 1;
}

.container {
  margin: 0 auto;
  max-width: 1920px;
  padding: 0 30px 0 179px;
}
@media (max-width: 1600px) {
  .container {
    padding: 0 40px 0 80px;
  }
}
@media (max-width: 1200px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (max-width: 480px) {
  .container {
    padding: 0 15px;
  }
}

.no-js .wrapper::before {
  content: "Пожалуйста, разрешите использовать JavaScript! Без него сайт грустит...";
  position: fixed;
  top: -300px;
  left: 0;
  width: 100%;
  padding: 20px;
  z-index: 99999;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 500;
  color: #f1535e;
  background-color: rgba(0, 0, 0, 0.75);
  animation-name: real;
  animation-delay: 3s;
  animation-duration: 7s;
  animation-fill-mode: forwards;
}

@keyframes real {
  0% {
    top: -300px;
  }
  10% {
    top: 0;
  }
  90% {
    top: 0;
  }
  100% {
    top: -300px;
  }
}
label {
  position: relative;
}

.wrong::before {
  content: "Заполните поле";
  position: absolute;
  bottom: 100%;
  left: 50%;
  padding: 15px 40px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  transform: translate(-50%, 0px);
  z-index: 1000;
}
.wrong:after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  border: 10px solid transparent;
  border-bottom: 10px solid rgba(0, 0, 0, 0.5);
  transform: translate(-50%, 0) rotate(180deg);
  z-index: 1000;
}

.btn-close {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border: none;
}
@media (max-width: 480px) {
  .btn-close {
    width: 40px;
    height: 40px;
    padding: 10px;
  }
}

.input-block {
  display: block;
}

.input-block__title {
  font-size: 1.25rem;
  font-weight: 700;
}
@media (max-width: 480px) {
  .input-block__title {
    font-size: 1.1rem;
  }
}

.input-block__input {
  width: 100%;
  padding: 15px 0;
  border: none;
  border-bottom: 1px solid #8D97A1;
  background-color: transparent;
  font-size: 1.25rem;
}
@media (max-width: 480px) {
  .input-block__input {
    padding: 8px 0;
    font-size: 0.8rem;
  }
}
.input-block__input::placeholder {
  color: #8D97A1;
}

.button-arrow {
  width: 90px;
  height: 90px;
  border: 1px solid #333333;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
}
@media (max-width: 1200px) {
  .button-arrow {
    width: 60px;
    height: 60px;
  }
}
@media (max-width: 480px) {
  .button-arrow {
    width: 40px;
    height: 40px;
  }
  .button-arrow svg {
    max-width: 50%;
  }
}

.button-arrow_prev .button-arrow__svg {
  transform: rotate(180deg);
}
@media (max-width: 320px) {
  .button-arrow_prev .button-arrow__svg {
    transform: rotate(0);
  }
}

@media (max-width: 320px) {
  .button-arrow_next .button-arrow__svg {
    transform: rotate(180deg);
  }
}

.button-arrow__svg_mobile {
  display: none;
}
@media (max-width: 320px) {
  .button-arrow__svg_mobile {
    display: block;
  }
}

.button-arrow__svg_desktop {
  display: block;
}
@media (max-width: 320px) {
  .button-arrow__svg_desktop {
    display: none;
  }
}

.title {
  margin: 0;
  font-weight: 500;
  font-size: 4.5rem;
  line-height: 1.18;
}

.title__small {
  font-size: 3.125rem;
  color: #60CEAE;
}
@media (max-width: 1200px) {
  .title__small {
    font-size: 2.57rem;
  }
}
@media (max-width: 768px) {
  .title__small {
    font-size: 2.4rem;
  }
}
@media (max-width: 360px) {
  .title__small {
    font-size: 1.8rem;
  }
}

.subtitle {
  margin: 0;
}

.subtitle__green {
  color: #4ECEA9;
  font-weight: 700;
}

.btn {
  margin: 0;
  height: 40px;
  padding: 0 28px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.03em;
  border-radius: 45px;
}

.btn_black {
  color: #ffffff;
  background-color: #333333;
  border: none;
}

.btn_big {
  height: 90px;
  padding: 0 48px;
  font-size: 1.375rem;
  line-height: 90px;
}
@media (min-width: 1360px) and (max-height: 900px) {
  .btn_big {
    height: 60px;
    padding: 0 35px;
    line-height: 60px;
    font-size: 1.2rem;
  }
}
@media (max-width: 1200px) {
  .btn_big {
    height: 60px;
    padding: 0 35px;
    line-height: 60px;
    font-size: 1.2rem;
  }
}
@media (max-width: 480px) {
  .btn_big {
    height: 40px;
    padding: 0 20px;
    line-height: 40px;
    font-size: 1.2rem;
  }
}

.btn_сallme {
  display: none;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  transform: translate(0, -100%);
  transition: transform 0.3s ease 0.8s;
  background: #EAF3F8;
}
@media (max-width: 768px) {
  .header {
    height: 70px;
    right: 0;
    transform: translate(0, 0);
  }
}

.header__top {
  position: relative;
  flex-shrink: 0;
}
@media (max-width: 1200px) {
  .header__top {
    padding-top: 10px;
  }
}
@media (max-width: 768px) {
  .header__top {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    padding-top: 20px;
  }
}

.header__container {
  position: relative;
  display: flex;
  min-height: 122px;
  padding-top: 59px;
}
@media (min-width: 1360px) and (max-height: 900px) {
  .header__container {
    padding-top: 29px;
  }
}
@media (max-width: 1200px) {
  .header__container {
    min-height: auto;
    padding-top: 10px;
  }
}
@media (max-width: 768px) {
  .header__container {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 0;
  }
}

.wrapper_loaded .header {
  transform: translate(0, 0);
}

.header__medium {
  position: relative;
  margin-left: auto;
  margin-right: 88px;
}
@media (max-width: 1200px) {
  .header__medium {
    margin-right: 5px;
    margin-top: 47px;
  }
}
@media (max-width: 768px) {
  .header__medium {
    display: none;
    width: 100%;
    margin-top: 20px;
    padding-top: 20px;
    padding-bottom: 15px;
    margin-right: auto;
  }
  .header__medium::before {
    content: "";
    position: absolute;
    top: 0;
    left: 17px;
    width: calc(100% - 34px);
    height: 1px;
    background-color: #454647;
  }
  .header__medium::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 17px;
    width: calc(100% - 34px);
    height: 1px;
    background-color: #454647;
  }
}
@media (max-width: 480px) {
  .header__medium {
    justify-content: flex-start;
    margin-left: 0;
  }
}

.header__bottom {
  flex-shrink: 0;
  margin-top: 0px;
}
@media (max-width: 1200px) {
  .header__bottom {
    margin-top: 30px;
  }
}
@media (max-width: 768px) {
  .header__bottom {
    display: none;
    padding-left: 20px;
  }
}

.header__close {
  display: none;
}
@media (max-width: 768px) {
  .header__close {
    position: absolute;
    display: block;
    top: 20px;
    right: 10px;
  }
}

.header__close-btn {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.3s;
}
.header__close-btn:hover {
  border: none;
}
.header__close-btn:hover .header__close-svg {
  fill: #4ECEA9;
}

.header__close-svg {
  display: none;
  fill: #ffffff;
  transform: translateX(2px);
  transition: all 0.3s;
}

.header__close-svg_mini {
  display: block;
  fill: #333333;
}

.header__button {
  font-size: 0.875rem;
  line-height: 40px;
  transition: all 0.3s;
}
@media (max-width: 768px) {
  .header__button {
    border: 1px solid #ffffff;
    background-color: transparent;
  }
}
@media (max-width: 768px) {
  .header__button:hover {
    border: 1px solid #4ECEA9;
    color: #4ECEA9;
    background-color: transparent;
  }
}

.header_active .header__close-svg {
  display: block;
}
.header_active .header__close-svg_mini {
  display: none;
}
.header_active .header__close-btn {
  width: 36px;
  height: 36px;
  border: 1px solid #ffffff;
  border-radius: 50%;
}
.header_active .header__close-btn:hover {
  border: 1px solid #4ECEA9;
}
@media (max-width: 768px) {
  .header_active {
    height: 100%;
    background: #333333;
    color: #ffffff;
  }
  .header_active .logo__text {
    display: none;
  }
  .header_active .header__medium, .header_active .header__bottom {
    display: block;
  }
}

.nav {
  display: flex;
}
@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .nav {
    justify-content: flex-start;
  }
}

@media (max-width: 768px) {
  .nav__item {
    display: flex;
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .nav__item {
    justify-content: flex-start;
  }
}

.nav__link {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 0 17px;
  font-weight: 700;
  font-size: 0.875rem;
  line-height: 2.714;
  letter-spacing: 0.03em;
  text-transform: lowercase;
  text-align: center;
  transition: all 0.3s;
}
.nav__link:hover {
  color: #4ECEA9;
}
.nav__link:hover .nav__link-svg {
  fill: #4ECEA9;
}
@media (max-width: 1200px) {
  .nav__link {
    font-size: 0.714rem;
    line-height: 1;
  }
}
@media (max-width: 768px) {
  .nav__link {
    padding: 30px 17px;
    font-weight: 400;
    font-size: 1.5rem;
  }
}
@media (max-width: 480px) {
  .nav__link {
    padding: 20px 17px;
    font-weight: 400;
    font-size: 1.2rem;
  }
}

.nav__link_active {
  color: #864DF9;
}
@media (max-width: 768px) {
  .nav__link_active {
    color: #4ECEA9;
    font-weight: 700;
  }
  .nav__link_active .nav__link-svg {
    fill: #4ECEA9;
  }
}

.nav__link-icon {
  display: none;
}
@media (max-width: 768px) {
  .nav__link-icon {
    display: block;
  }
  .nav__link-icon svg {
    fill: #ffffff;
    transition: all 0.3s;
  }
}

.page {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  background-color: #EAF3F8;
}

.wrapper_free .page {
  position: relative;
}

.page__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.page__pagination {
  position: absolute;
  top: 236px;
  right: 20px;
  height: 516px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 3;
  transform: translate(100px, 0);
  opacity: 0;
